.title {
  font-weight: 700;
  font-size: 36px;
  line-height: 60px;
  &_fz12 {
    font-size: 12px;
    line-height: 18px;
    font-weight: 500;
  }
  &_fz14 {
    font-size: 14px;
    line-height: 21px;
    margin-bottom: 0;
  }
  &_fz16 {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 6px;
  }
  &_fz20 {
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 0;
  }
  &_fz48 {
    font-size: 48px;
  }
  &__section-title {
    text-align: center;
    position: relative;
    @include before_circle;
    &::before {
      left: 50%;
      transform: translateX(-50%);
    }
  }
}
